import choseDayBar from 'components/chose-day-bar';
import { timeSheetQuery } from '@/api/time-sheet';
import Swiper from '../../../static/swiper-3.3.1.min.js'
import {schedulingInfo} from '@/api/scheduling'

export default {
  name: 'timeSheet',
  components: {choseDayBar},
  data() {
    return {
      weekList: ["", "", "", "", "", "", ""],
      blankList: "",//
      dayCount: "",//
      typeList: [
        {type: ""},
        {type: ""},
        {type: ""},
        {type: ""},
        {type: ""},
      ],
      choseSpan: false,
      choseFlag: "",
      choseNowDay: "",// /
      sheetDetailList: "",//
      sheetList: "",
      abnormal: false,// 
      dayDetailList: "",//
      nowMonth: "",
      showNowDay: "",
      abnormalFlag: "",
      overTime: "",
      maxLength: "",
      option:{
        autoplay:false,
      },
      scheduledDate:"",
      backModel: [
        {},
        {},
        {},
        {},
        {},
      ],
    }
  },
  methods: {

    lunbo () {
      let that = this;
      new Swiper('.gallery-top', {
        spaceBetween: 10,
        initialSlide: 2,
        loop: true,
        loopedSlides: 5,
        touchRatio: 0.5,
        centeredSlides: true,
        autoHeight: true,
        effect: 'coverflow',
        slidesPerView: 1.2,
        coverflow: {
          rotate: 0,
          stretch: -13,
          depth: 70,
          modifier: 3,
          slideShadows: true
        },
        speed: 500,
        iOSEdgeSwipeDetection : true,//trueIOSUIWebViewswiper
        preventLinksPropagation : true,

        //
        onSlidePrevStart: function (swiper) {

          if(swiper.activeIndex===6||swiper.activeIndex===5){
            swiper.slideTo(2, 1000, false);
            //
            that.$refs.editUser.lastMonth();
          }else{
            that.$refs.editUser.lastMonth();
          }
        },
        //
        onSlideNextStart: function (swiper) {
          //onSlideNextStartswiper.activeIndex===7slide
          if(swiper.activeIndex===7){

          }else if(swiper.activeIndex===8||swiper.activeIndex===9){
            swiper.slideTo(2, 1000, false);//activeIndex>9activeIndex9
            that.$refs.editUser.nextMonth();
          }else {
            that.$refs.editUser.nextMonth();
          }
        },


      });
    },
    getChange(msg){//
      this.choseNowDay = msg.slice(0, 4) + "-" + msg.slice(5, 7);
      this.nowMonth = msg.slice(5, 7);
      this.nowYear = msg.slice(0, 4);
      this.blankList = this.getBlank(this.nowYear, this.nowMonth - 1) - 1;
      this.dayCount = this.getDayCount(this.nowYear, this.nowMonth);
      this.getMonthList(this.choseNowDay);
      console.log("-----------" + this.choseNowDay);
    },
    choseDayBar(day, index){//
      this.choseFlag = index;
      if (this.choseNowDay.length === 10) {
        if (day < 10) {
          this.choseNowDay = this.choseNowDay.slice(0, 8) + "0" + day;
        } else {
          this.choseNowDay = this.choseNowDay.slice(0, 8) + day;
        }
      } else if (this.choseNowDay.length === 7) {
        if (day < 10) {
          this.choseNowDay = this.choseNowDay + "-" + "0" + day;
        } else {
          this.choseNowDay = this.choseNowDay + "-" + day;
        }
      }
      console.log("" + this.choseNowDay);
      this.getDayList(this.choseNowDay);
      this.getScheduling({"hrEmpId": 12229, "hrScheduleDate": this.choseNowDay});
    },
    getBlank(year, month){//
      let blankNum = new Date(year, month, "01");//
      if (blankNum.getDay() === 0) {
        return 7;
      }
      console.log("" + blankNum.getDay());
      return blankNum.getDay();
    },
    getDayCount(year, month){//
      let dayNum = new Date(year, month, 0);
      return dayNum.getDate();
    },
    getDayList(){
      timeSheetQuery({
          "clockDate": this.choseNowDay,
        },
      ).then(response => {
        if (response.data && response.data.rows) {
          this.dayDetailList = response.data.rows;
          this.isLoadingData = false;
        } else {
          this.isLoadingData = false;
        }
      }).catch(error => {
        this.isLoadingData = false;
      });
    },
    getMonthList(param){
      timeSheetQuery({
          "clockDate": param,
        },
      ).then(response => {
        if (response.data && response.data.rows) {
          this.sheetDetailList = response.data.rows;
          this.maxLength = this.sheetDetailList.length;
          for (let i = 0; i < this.sheetDetailList.length; i++) {
            if (this.sheetDetailList[i].abnormal !== null) {
              this.abnormalFlag = i;
            }
            if (this.sheetDetailList[i].overTime === "Y") {
              this.overTime = i;
              console.log("" + this.overTime);
            }
          }
          this.isLoadingData = false;
        } else {
          this.isLoadingData = false;
        }
      }).catch(error => {
        this.isLoadingData = false;
      });
    },

    //
    getScheduling(params){
      schedulingInfo(params).then(response => {
        if (response.data && response.data.rows) {
          this.scheduledDate = response.data.rows[0].detailList;
          console.log("sd"+this.scheduledDate);
          this.isLoadingData = false;
        } else {
          this.isLoadingData = false;
        }
      }).catch(error => {
        this.isLoadingData = false;
      });
    },

  },
  mounted() {
    this.nowMonth = this.nowMonth.slice(0, 4) + "-" + this.nowMonth.slice(5, 7);
    this.getMonthList(this.nowMonth);//
    this.getDayList(this.choseNowDay);//
    console.log("" + this.nowMonth);
    console.log("" + this.choseNowDay);
    console.log("" + this.blankList);
    this.lunbo();
    this.getScheduling({"hrEmpId": 12229, "hrScheduleDate": this.choseNowDay});
  },
  created: function () {
    let nowDate = new Date();
    let nowYear = nowDate.getFullYear();
    let nowMonth = nowDate.getMonth()+1;//0-11
    let nowDay = nowDate.getDate();
    if (nowDay < 10) {
      nowDay = "0" + nowDay;
    } else {
      nowDay = nowDate.getDate();
    }
    this.choseNowDay = nowYear + "-" + nowMonth + "-" + nowDay;//  
    this.dayCount = this.getDayCount(nowYear, nowMonth);// 
    this.blankList = this.getBlank(nowYear, nowMonth - 1) - 1;//0-11  
    if (nowMonth < 10) {
      this.nowMonth = nowYear + "" + "0" + nowMonth + "";
      console.log("" + this.nowMonth)
    } else {
      this.nowMonth = nowYear + "" + nowMonth + "";
      console.log("" + this.nowMonth)
    }
    console.log(""+this.nowMonth);
    return this.nowMonth;//  20171106
  }
}



