
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'

export default {
name: 'choseDayBar',
components: {Datetime, Group},
props:['nowMonth'],//
data() {
return {
choseDay:"",
}
},
methods: {
change() {
this.$emit('transfer',this.choseDay)//
},
lastMonth(){
this.$emit('lastMonth');
let nowMonth = parseInt(this.choseDay.slice(5, 7));
let nowYear = parseInt(this.choseDay.slice(0, 4));
if (nowMonth === 1) {
nowMonth = 12;
this.choseDay = (nowYear - 1) + "" + nowMonth + "";
this.hrScheduleDate = (nowYear - 1) + "-" + nowMonth + "-01"
} else if (nowMonth <= 10) {
this.choseDay = nowYear + "" + "0" + (nowMonth - 1) + "";
this.hrScheduleDate = nowYear + "-" + "0" + (nowMonth - 1) + "-01";
} else {
this.choseDay = nowYear + "" + (nowMonth - 1) + "";
this.hrScheduleDate = nowYear + "-" + (nowMonth - 1) + "-01";
}
},
nextMonth(){
this.$emit('nextMonth');
let nowMonth = parseInt(this.choseDay.slice(5, 7));
let nowYear = parseInt(this.choseDay.slice(0, 4));
if (nowMonth === 12) {
nowMonth = 1;
this.choseDay = (nowYear + 1) + "" + "0" + nowMonth + "";
this.hrScheduleDate = (nowYear + 1) + "-" + "0" + nowMonth + "-01";
} else if (nowMonth >= 9) {
this.choseDay = nowYear + "" + (nowMonth + 1) + "";
this.hrScheduleDate = nowYear + "-" + "0" + (nowMonth + 1) + "-01";
} else {
this.choseDay = nowYear + "" + "0" + (nowMonth + 1) + "";
this.hrScheduleDate = nowYear + "-" + "0" + (nowMonth + 1) + "-01";
}
},
},
created: function () {
this.choseDay = this.nowMonth;
}
}



